<template>
  <v-card class="mx-auto overflow-hidden" height="auto">
    <h1>Implementation Navigation Bar:</h1>
    <v-app-bar
      xabsolute
      color="#363636"
      dark
      xshrink-on-scroll
      xdense
      xprominent
      xscroll-target="#scrolling-techniques-4"
    >
      <v-container>
        <v-row>
          <!-- RBS Logo -->
          <v-col sm="1" align="left" class="mt-6">
              <a href="/">
                <img
                  height="45"
                  width="45"
                  :src="fromAssetsFolder('roundabout_logo.png')"
                />
              </a>
          </v-col>
          <!-- Top Navigation Icons+Label -->
          <v-col sm="10" class="d-flex justify-start pt-6">
            <router-link
              v-for="tab in navigation.tabs"
              @click.native="setActiveUrl(tab)"
              :to="tab.url"
              :key="tab.text"
              class="router-link-nav"
            >
              <div class="d-flex flex-column pl-6 pt-1">
                <v-icon x-large>{{ tab.icon }}</v-icon>
                <p class="navigation-icon-label">{{ tab.text }}</p>
              </div>
            </router-link>
          </v-col>

          <!-- Settings Icon right -->
          <v-col sm="1" class="pt-6" align="right">
            <v-menu
              right
              dark
              :close-on-content-click="false"
              open-on-hover
              bottom
              offset-y
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon v-bind="attrs" v-on="on">
                  <v-icon x-large>mdi-account-cog</v-icon>
                </v-btn>
              </template>
              <v-list>
                <v-list-group
                  v-for="settingItem in navigation.settings"
                  :key="settingItem.text"
                  v-model="settingItem.active"
                  :prepend-icon="settingItem.action"
                  no-action
                  :class="
                    settingItem.submenu ? '' : 'no_submenu_hide_submenu_icon'
                  "
                  :to="true"
                  :value="true"
                  @click.native="setActiveUrl(settingItem)"
                >
                  <template v-slot:activator>
                    <v-list-item>
                      <v-list-item-content>
                        <v-list-item-title>{{
                          settingItem.text
                        }}</v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </template>

                  <!-- submenu items -->
                  <v-list-item
                    v-for="subItem in settingItem.submenu"
                    :key="subItem.text"
                    @click.native="setActiveUrl(subItem)"
                  >
                    <v-list-item-content>
                      <v-list-item-title>{{ subItem.text }}</v-list-item-title>
                    </v-list-item-content>

                    <v-list-item-action>
                      <v-icon>{{ subItem.action }}</v-icon>
                    </v-list-item-action>
                  </v-list-item>
                </v-list-group>
              </v-list>
            </v-menu>
          </v-col>
        </v-row>
      </v-container>
    </v-app-bar>
    <!-- Dev questions -->
    <h1>Dev</h1>
    <ul>
      <li>
        .container (red) max-width met media-query voor verschillende scherm
        breedtes
      </li>
      <li>Op Mobile moeten de labels weg.. en dan size berekenen met <a href="https://codepen.io/jkarczm/pen/oNgraYR?editors=1010">$vuetify.breakpoint</a></li>
      <li>Hoe kan Settings > Account settings default opengeklapt worden?</li>
    </ul>

    <!-- design screenshot -->
    <h1>Design:</h1>
    <img src="https://i.imgur.com/GE0u649.png" style="max-height: 300px" />
  </v-card>
</template>

<script>
import { mapActions, mapState } from "vuex";

import fromAssetsFolder2 from "./../utils";

export default {
  name: "Navigation",
  components: {},
  data() {
    return {
      // data structure van verticale navigatie aangepast
      navigation: {
        tabs: [
          {
            icon: "mdi-account-group-outline",
            text: "Community",
            url: "/navhor",
            active: false,
          },
          // {
          //   icon: "mdi-chat",
          //   text: "Chat",
          //   url: "/chat",
          //   active: false,
          // },
          {
            icon: "mdi-trending-up",
            text: "Your Campaigns",
            url: "/navhor",
            active: false,
          },
          {
            icon: "mdi-radar",
            text: "Your Audience trends",
            url: "/navhor",
            active: false,
          },
        ],
        settings: [
          {
            icon: "mdi-movie-outline",
            text: "Video Library",
            url: "/videolibrary",
            active: false,
          },
          {
            icon: "mdi-help-circle",
            text: "FAQ",
            url: "/faq",
            active: false,
          },
          {
            icon: "mdi-chevron-up",
            "icon-alt": "mdi-chevron-down",
            text: "Account settings",
            active: false,
            model: false,
            submenu: [
              {
                text: "Settings",
                url: "/account",
                icon: "mdi-tune",
                active: false,
              },
              {
                text: "Notification",
                url: "/notification/devices",
                icon: "mdi-bell-outline",
                active: false,
              },
              {
                text: "Social Media",
                url: "/account/settings/socialmedia",
                icon: "mdi-share-variant-outline",
                active: false,
              },
            ],
          },
        ],
      },
    };
  },
  computed: {
    ...mapState({
      totalUnseenChatMessages: "totalUnseenChatMessages",
    }),

    user: function () {
      return this.$store.state.user;
    },
  },
  methods: {
    fromAssetsFolder(img) {
      console.log(img);
      return "./static/" + img;
      return fromAssetsFolder2(img);
    },
    setActiveUrl(navigate) {
      if (!navigate.submenu) window.alert("Naar ► " + navigate.text);
    },
  },
  mounted() {
    //this.setActiveUrl();
  },
};
</script>
<style>
.no_submenu_hide_submenu_icon .v-icon {
  /* I read v-if doesn't go well with v-for */
  display: none;
}
h1 {
  padding: 1em 0;
}
</style>

<style lang="scss" scoped>
.router-link-nav {
  --RBSlabelcolor: white;
  --RBShovercolor: #009788;
  text-decoration: none;
  color: var(--RBSlabelcolor);
}
// color both icon and label
a:hover i,
a:hover div {
  color: var(--RBShovercolor);
}
.container {
  border: 1px solid red;
  // margin-left:unset;
  // margin-right:unset;
}
.navigation-icon-label {
  xdisplay: none;
}
</style>
